<template>
 <div id="Home">
     <Nav class="home-nav"><div slot="center">购物街</div></Nav>
      <home-swiper :banners="banners" />
        <Recommend-View :recommends="recommends"/>
        <feature-view/>
        <Tabcontrol :titles="['流行','新款','精选']" @tabClick='tabClick'></Tabcontrol>
        <Goodlist :goods="goods[ctype].list">

        </Goodlist>

       
        
 </div>
</template>

<script>
import HomeSwiper from './childComps/HomeSwiper'
import RecommendView from './childComps/RecommendView'
import FeatureView from './childComps/FeatureView'

import Nav from 'components/common/nav.vue';
import Goodlist from 'components/content/goods/goodlist.vue';

import Tabcontrol from 'components/common/tabcontrol/tabcontrol';
import { homedata,homegood } from'network/home.js'
export default {
  name: 'Home',
  data(){
    return {
    banners:[],
    recommends:[],
    goods:{
      'pop':{page:0,list:[]},
      'new':{page:0,list:[]},
      'sell':{page:0,list:[]}
    },
    ctype:'pop'
    
  }

  },
  components: {
    Nav,
    HomeSwiper,
    RecommendView,
    FeatureView,
    Tabcontrol,
    Goodlist
  },
  created(){
      this. homedata(),
     this. homegood('pop'),
     this. homegood('new'),
     this. homegood('sell')
  },
  methods:{ 
    tabClick(index){
      switch(index){
        case 0:
        this.ctype='pop'
        break
         case 1:
        this.ctype='new'
        break
         case 2:
        this.ctype='sell'
        break

      }

    },

    homedata(){
      homedata().then (res=>{
        // console.log(res.data)
        this.banners=res.data.banner.list
        this.recommends=res.data.recommend.list
      })
  },
homegood(type){
 const page=this.goods[type].page+1
 homegood(type,page).then(res=>{
        console.log(res.data.list)

       this.goods[type].list.push(...res.data.list) 
      this.goods[type].page+=1
      })
}
    
  }
}
</script>

<style scoped>
#home{
    /* height: 100vh;
    position: relative; */

  padding-top: 44px;
}
.home-nav{
  background-color: var(--color-tint);
    color: #fff;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;

}
.tab-control {
    position: relative;
    z-index: 9;
  }
</style>
